import request from './javaRequest'

// 新增点单
export const addOrder = (data) => {
  return request({
    url: '/services/serveWork/add',
    data: data,
  })
}

// 获取点单列表
export const getMyOrderList = (data) => {
  return request({
    url: '/services/serveWork/selectMyOrder',
    data: data,
  })
}

export const getOrderInfo = (serveWorkId) => {
  return request({
    url: `/services/serveWork/info/${serveWorkId}`,
    method: 'get'
  })
}

// 获取点单配置
// /setting/info/{settingId}
export const getOrderSetting = (settingId) => {
  return request({
    url: `/services/setting/info/${settingId}`,
    method: 'get',
  })
}
